import http from "./http";

interface ApiResponse<T = any> {
    code: number;
    message: string;
    data: T;
}

export function get<T = any>(url: string, params?: Record<string, unknown>): Promise<ApiResponse<T>> {
    return http.get(url, { params });
}

export function post<T = any>(url: string, data?: unknown): Promise<ApiResponse<T>> {
    return http.post(url, data);
}