import { RouteObject } from "react-router";
import { componentMap } from "../router/routerMap";
import RequireAuth from "../utils/RequireAuth";

interface MenuType {
    icon: string;
    key: string;
    label: string;
    children?: MenuType[];
}

export function generateRoutes(menu: MenuType[]): RouteObject[] {
    return menu.map((item) => {
        const hasChildren = !!item.children?.length;
        const Component = componentMap[item.key];

        if (!Component && !hasChildren) {
            console.warn(` componentMap ${item.key}`);
        }

        const route: RouteObject = {
            path: item.key,
            element: hasChildren
                ? undefined
                : Component
                    ? (
                        <RequireAuth needLogin={true} redirectTo="/login">
                            <Component />
                        </RequireAuth>
                    )
                    : <div>{item.key}</div>,
        };

        if (hasChildren) {
            route.children = generateRoutes(item.children!);
        }

        return route;
    });
}
